*, *:before, *:after {
  box-sizing: inherit;
}

html {
  font-size: 62.5%;
  box-sizing: border-box;
  height: 100%;
}

body {
  font-family: "Open Sans", sans-serif;
  font-size: 1.6rem;
  min-height: 100%;
}

.contenedor {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

.contenedor-sm {
  max-width: 70rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
}

h1, h2, h3 {
  font-family: "Inter", sans-serif;
  margin: 0 0 2.5rem 0;
  font-weight: 900;
}

h1 {
  font-size: 6rem;
}

h2 {
  font-size: 5rem;
}

h3 {
  font-size: 4rem;
}

p {
  font-size: 2rem;
  color: #6b7280;
  line-height: 1.8;
}

.descripcion-pagina {
  text-align: center;
}

.tagline {
  font-family: "Inter", sans-serif;
  font-weight: 900;
  color: #000;
  font-size: 3rem;
  text-align: center;
}

/** Media Queries **/
.formulario {
  margin-top: 5rem;
}
.formulario .campo {
  display: flex;
  align-items: center;
  margin-bottom: 2rem;
}
.formulario label {
  flex: 0 0 10rem;
}
.formulario input {
  flex: 1;
  padding: 1rem;
  border: none;
}

.acciones {
  margin-top: 5rem;
}
@media (min-width: 768px) {
  .acciones {
    display: flex;
    justify-content: space-between;
  }
}
.acciones a {
  display: block;
  text-align: center;
  margin-bottom: 2rem;
  color: #000;
}
.acciones a:last-of-type {
  margin-bottom: 0;
}

.login {
  margin-top: 5rem;
}
.login .uptask {
  background: linear-gradient(to right, #0891B2 0%, rgb(91.3548387097, 217.3064516129, 247.6451612903) 100%);
  color: transparent;
  background-clip: text;
  font-size: 7rem;
  text-align: center;
}
@media (min-width: 768px) {
  .login .uptask {
    font-size: 10rem;
  }
}
.login input[type=submit] {
  background-color: #0891B2;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  font-size: 1.6rem;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
}
@media (min-width: 768px) {
  .login input[type=submit] {
    width: auto;
  }
}
.login input[type=submit]:hover {
  background-color: rgb(5.8064516129, 105.2419354839, 129.1935483871);
  cursor: pointer;
}
.login .acciones a {
  color: #0891B2;
}

.crear {
  margin-top: 5rem;
}
.crear .uptask {
  background: linear-gradient(to right, #7C3AED 0%, rgb(217.223255814, 198.1906976744, 249.8093023256) 100%);
  color: transparent;
  background-clip: text;
  font-size: 7rem;
  text-align: center;
}
@media (min-width: 768px) {
  .crear .uptask {
    font-size: 10rem;
  }
}
.crear input[type=submit] {
  background-color: #7C3AED;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  font-size: 1.6rem;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
}
@media (min-width: 768px) {
  .crear input[type=submit] {
    width: auto;
  }
}
.crear input[type=submit]:hover {
  background-color: rgb(95.3302325581, 20.4279069767, 223.5720930233);
  cursor: pointer;
}
.crear .acciones a {
  color: #7C3AED;
}